.rakuie-omakase {
  margin: 130px 0 80px;
  padding: 100px 60px 60px;
  border: 2px solid #e45213;
  position: relative;
}

.rakuie-omakase h2 {
  position: absolute;
  top: -55px;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  font-size: 4.2rem;
  letter-spacing: 0.1em;
  line-height: 1.3;
  font-weight: bold;
  background: #fff;
  padding: 0 20px;
  text-align: center;
  color: #e45213;
  width: max-content;
}

.rakuie-omakase p {
  font-size: 2.4rem;
  line-height: 2em;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
  letter-spacing: 1px;
}

.rakuie-omakase p strong {
  font-size: 3.6rem;
  margin: 0 0.3em;
  background: linear-gradient(transparent 50%, #ffff01 50%);
}

@media screen and (max-width: 767px) {
  .rakuie-omakase {
    margin: 21.33333vw 0 10.66667vw;
    padding: 13.33333vw 5.33333vw 5.33333vw;
    border: 2px solid #e45213;
  }
  .rakuie-omakase h2 {
